*,
*:before,
*:after {
  text-decoration: none;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

ul,
ul * {
  text-decoration: none;
  list-style: none;
  margin: 0;
  padding: 0;
}

html {
  font-size: 15px;
}

.App {
  width: 100vw;
  height: 100vh;
  position: relative;
}

#three-app, #pages .section {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

#pages .methodologies .cards, #pages .methodologies .methodology {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.menu li, #pages .methodologies .card {
  -webkit-transition: -webkit-transform 400ms ease;
  transition: -webkit-transform 400ms ease;
  -o-transition: transform 400ms ease;
  transition: transform 400ms ease;
  transition: transform 400ms ease, -webkit-transform 400ms ease;
}

.menu li:hover, #pages .methodologies .card:hover {
  -webkit-transform: scale(1.2);
          -ms-transform: scale(1.2);
      transform: scale(1.2);
  cursor: pointer;
}

.menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 2rem;
  background: black;
  color: white;
}

.menu li {
  padding: 1rem;
  margin: 0 1rem;
}

#pages .section {
  background: rgba(128, 128, 128, 0.3);
  padding-top: 2em;
}

#pages .methodologies .cards {
  color: white;
}

#pages .methodologies .card {
  width: 280px;
  height: 400px;
  margin: 0 1em;
  padding: 1em;
  background: black;
  border: 1px white dotted;
}

#pages .methodologies .methodology {
  -ms-flex-direction: column;
  flex-direction: column;
  color: black;
  background: white;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  opacity: 0;
}

#pages .methodologies .methodology.open {
  opacity: 1;
}

#pages img {
  width: 300px;
}



